import vm from '@/main';
import axios from 'axios';

// APIhttps://github.com/mzabriskie/axios
// axios.defaults.baseURL = 'http://localhost:6060';
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

/**
 * ajax
 * @param method {String} 'get' / 'post'
 * @param url {String} url
 * @param data {Object} 
 * @param successMsg {String} 
 */
function ajax({method, url, data, successMsg}) {
  method = method || 'post';  // post

  if (!url) {
    vm.$notify({
      title: '',
      message: 'url',
      type: 'warning',
      duration: 3000
    });

    return;
  }

  let args = {
    method: method,
    url: url
  };

  // methodpostdatagetparams
  method = method.toLowerCase();
  if (method === 'post') {
    args.data = data;
  } else if (method === 'get') {
    args.params = data;
  } else {
    args.data = data;
  }

  return axios(args)
    .then(function (response) {
      return new Promise((resolve, reject) => {
        if (response.data.error_code === 0) {
          if (!!successMsg) {
            vm.$notify({
              title: '',
              message: successMsg,
              type: 'success',
              duration: 3000
            });
          }
          resolve(response.data);
        } else {
          vm.$notify({
            title: '',
            message: response.data.msg,
            type: 'error',
            duration: 3000
          });
          reject(response.data);
        }
      });
    }, function (response) {
      return new Promise((resolve, reject) => {
        vm.$notify({
          title: '',
          message: response.message,
          type: 'error',
          duration: 4000
        });
        reject(response);
      })
    })
}

export default ajax;



