<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.groupName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-input v-model="filters.teacher" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getPageGroups(1)"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <template>
      <el-table
        :data="groups"
        v-loading="listLoading"
        height="500" style="width: 100%;">
        <el-table-column prop="groupName" label=""></el-table-column>
        <el-table-column prop="teacher" label=""></el-table-column>
        <el-table-column prop="linkman" label=""></el-table-column>
        <el-table-column prop="phone" label=""></el-table-column>
        <el-table-column prop="creditScore" label="" width="100"></el-table-column>
        <el-table-column label="" width="150">
          <template scope="scope">
            <el-button size="small" @click="showCreditBox(scope.row)"></el-button>
            <el-button type="info" size="small" @click="handleCreditList(scope.row)"></el-button>
          </template>
        </el-table-column>
        <el-table-column label="" width="350">
          <template scope="scope">
            <el-button size="small" @click="handleEdit(scope.row)"></el-button>
            <el-button size="small" type="danger" @click="handleDel(scope.row)"></el-button>
            <el-button size="small" type="info" @click="checkUsers(scope.row)"></el-button>
            <el-button size="small" type="info" @click="checkLoans(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </template>

    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom:10px;">
      <el-pagination
        layout="prev, pager, next"
        @current-change="getPageGroups"
        :page-size="pageSize"
        :total="total" style="float:right;">
      </el-pagination>
    </el-col>

    <!---->
    <el-dialog :title="editFormTtile" v-model="editFormVisible" @close="resetForm('editForm')">
      <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
        <el-form-item label="" prop="groupName">
          <el-input v-model="editForm.groupName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="teacher">
          <el-input v-model="editForm.teacher" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="linkman">
          <el-input v-model="editForm.linkman" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="editForm.phone" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"> </el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading">{{btnEditText}}</el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" v-model="addCreditVisible" @close="resetForm('addCreditForm')">
      <el-form :model="addCreditForm" label-width="100px" :rules="addCreditFormRules" ref="addCreditForm">
        <el-form-item label="/" prop="type">
          <el-radio-group v-model="addCreditForm.type">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="score">
          <el-input-number v-model="addCreditForm.score" :min="1" :max="20"></el-input-number>
        </el-form-item>
        <el-form-item label="">
          <el-input
            type="textarea"
            :rows="2"
            placeholder=""
            v-model="addCreditForm.message">
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addCreditForm.loanId" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addCreditVisible = false"> </el-button>
        <el-button type="primary"
                   @click.native="addCreditSubmit"
                   :loading="addCreditLoading">{{btnAddCreditText}}
        </el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" v-model="creditListVisible">
      <el-table :data="formattedCreditList"
                v-loading="creditListLoading"
                :row-class-name="creditListRowClass">
        <el-table-column type="expand">
          <template scope="scope">
            <el-form label-position="left" inline class="credit-table-expand">
              <el-form-item label="">
                <span>{{ scope.row.message }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="formattedType" label="/"></el-table-column>
        <el-table-column prop="score" label=""></el-table-column>
        <el-table-column prop="loanId" label=""></el-table-column>
        <el-table-column label="">
          <template scope="scope">
            <el-button size="small" type="danger" @click="handleCreditDel(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-col :span="24" style="padding:10px 0;">
        <el-pagination
          layout="prev, pager, next"
          @current-change="getPageCreditList"
          :current-page="creditListPage"
          :page-size="creditListPageSize"
          :total="creditListTotal" style="float:right;">
        </el-pagination>
      </el-col>
    </el-dialog>
  </section>
</template>

<script>
  import util from '../../assets/js/util'
  import NProgress from 'nprogress'
  import api from '@/api/api'

  export default {
    data() {
      return {
        filters: {
          groupName: '',
          teacher: '',
        },
        groups: [],
        total: 0,
        page: 1,
        pageSize: 10,
        listLoading: false,
        editFormVisible: false,//
        editFormTtile: '',//
        editForm: { //
          id: 0,
          groupName: '',
          teacher: '',
          linkman: '',
          phone: '',
        },
        editFormRules: {
          groupName: [
            {required: true, message: '', trigger: 'blur'}
          ],
          teacher: [
            {required: true, message: '', trigger: 'blur'}
          ],
          linkman: [
            {required: true, message: '', trigger: 'blur'}
          ],
          phone: [
            {required: true, message: '', trigger: 'blur'}
          ]
        },
        editLoading: false,
        btnEditText: ' ',
        addCreditVisible: false,
        addCreditForm: { //
          groupId: 0,
          type: '',
          score: '',
          message: '',
          loanId: '',  // 
        },
        addCreditFormRules: {
          type: [
            {required: true, type: 'number', message: '/', trigger: 'change'}
          ],
          score: [
            {required: true, type: 'number', message: '', trigger: 'blur'}
          ]
        },
        addCreditLoading: false,
        btnAddCreditText: ' ',
        creditListVisible: false, // 
        creditList: [], // 
        creditListGroupId: '',
        creditListTotal: 0,
        creditListPage: 1,
        creditListPageSize: 5,
        creditListLoading: false,
      }
    },
    computed: {
      formattedCreditList() {
        let formattedCreditList = [];
        let creditMapArr = [{
          label: '',
          value: 1
        }, {
          label: '',
          value: 2
        }];

        formattedCreditList = this.creditList.map((item, index) => {
          for (let type of creditMapArr) {
            if (type.value === item.type) {
              item.formattedType = type.label;

              break;
            }
          }

          return item;

        });

        return formattedCreditList;
      }
    },
    methods: {
      getPageGroups(val) { // 
        this.page = val;
        this.getGroups();
      },
      //
      getGroups() {
        let checkedFilters = {};

        for (let item in this.filters) {
          if (this.filters.hasOwnProperty(item) && !!this.filters[item].toString()) {
            checkedFilters[item] = this.filters[item];
          }
        }

        let para = {
          page: this.page,
          pageSize: this.pageSize,
          filters: checkedFilters
        };

        this.listLoading = true;
        api.getGroupListPage(para).then((res) => {
          this.total = res.data.total;
          this.groups = res.data.groups;
          this.listLoading = false;
        }).catch(() => {});
      },
      //
      handleDel: function (row) {
        //console.log(row);
        let _this = this;
        this.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          _this.listLoading = true;
          let para = {
            id: row.id,
          };

          api.delGroup(para).then(res => {
            _this.listLoading = false;
            if (res.error_code === 0) {
              _this.$notify({
                title: '',
                message: '',
                type: 'success'
              });
              _this.getGroups();
            }
          }).catch(() => {
            console.log('error');
          });

        }).catch(() => {
        });
      },
      //
      handleAdd: function () {
        this.editFormVisible = true;
        this.editFormTtile = '';
        this.editForm.id = 0;
        this.editForm.groupName = '';
        this.editForm.teacher = '';
        this.editForm.linkman = '';
        this.editForm.phone = '';
      },
      //
      handleEdit: function (row) {
        this.editFormVisible = true;
        this.editFormTtile = '';
        this.editForm.id = row.id;
        this.editForm.groupName = row.groupName;
        this.editForm.teacher = row.teacher;
        this.editForm.linkman = row.linkman;
        this.editForm.phone = row.phone;
      },
      // or 
      editSubmit: function () {
        let _this = this;

        _this.$refs.editForm.validate((valid) => {
          if (valid) {
            _this.$confirm('', '', {}).then(() => {
              _this.editLoading = true;
              _this.btnEditText = '';

              api.updateGroup(_this.editForm).then((res) => {
                if (res.error_code === 0) {
                  _this.$notify({
                    title: '',
                    message: '',
                    type: 'success'
                  });
                  _this.editLoading = false;
                  _this.btnEditText = ' ';
                  _this.resetForm('editForm');
                  _this.editFormVisible = false;
                  _this.getGroups();
                }
              })
            }).catch(() => {
            });

          }
        });

      },
      // 
      showCreditBox: function (row) {
        this.addCreditVisible = true;
        this.addCreditForm.groupId = row.id;
        this.addCreditForm.type = '';
        this.addCreditForm.score = '';
        this.addCreditForm.message = '';
        this.addCreditForm.loanId = '';
      },
      addCreditSubmit: function () {
        let _this = this;
        _this.$refs.addCreditForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              _this.addCreditLoading = true;
              _this.btnAddCreditText = '';

              api.addCredit(_this.addCreditForm).then((res) => {
                if (res.error_code === 0) {
                  _this.$notify({
                    title: '',
                    message: '',
                    type: 'success'
                  });
                  _this.addCreditLoading = false;
                  _this.btnAddCreditText = ' ';
                  _this.resetForm('addCreditForm');
                  _this.addCreditVisible = false;
                  _this.getGroups();
                }
              })
            }).catch(() => {
            });
          }
        })
      },
      handleCreditList: function (row) {
        this.creditListVisible = true;
        this.creditListGroupId = row.id;
        this.getPageCreditList(1);
      },
      getPageCreditList: function (val) {
        this.creditListPage = val;
        this.checkCreditList();
      },
      checkCreditList: function () {
        let para = {
          groupId: this.creditListGroupId,
          page: this.creditListPage,
          pageSize: this.creditListPageSize
        };

        this.creditListLoading = true;
        api.getCreditListPage(para).then((res) => {
            console.log(2222222);
            console.log(res.data);
          this.creditListTotal = res.data.total;
          this.creditList = res.data.creditList;
          this.creditListLoading = false;
        }).catch(() => {

        });
      },
      handleCreditDel: function (row) {
        let _this = this;
        this.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          _this.creditListLoading = true;
          let para = {
            id: row.id,
          };

          api.delCredit(para).then(res => {
            _this.creditListLoading = false;
            if (res.error_code === 0) {
              _this.$notify({
                title: '',
                message: '',
                type: 'success'
              });
              _this.checkCreditList();
            }
          }).catch(() => {
            console.log('error');
          });

        }).catch(() => {
        });
      },
      creditListRowClass(row, index) {
        if (row.type === 2) {
          return 'bad-credit';
        }

        return '';
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    },
    mounted() {
      this.getPageGroups(1);
    }
  }
</script>

<style scoped>

</style>



